.section-decor {
  position: relative;
  z-index: 11;
  display: flex;
  flex-direction: column;

  &__wrapper {
    flex-grow: 1;
    width: 100%;
  }

  &__bg,
  &__img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  &__img {
    height: auto;
  }

  &__bg {
    height: 100%;
  }
}